@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom hover glow effect with purple aura */
.hover-glow:hover {
  box-shadow: 0 0 10px 2px rgba(155, 89, 182, 0.8),
              0 0 20px 4px rgba(155, 89, 182, 0.8);
  transform: scale(1.02);
  transition: transform 0.3s, box-shadow 0.3s;
}

/* Scrollbar styling */
.overlay-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: rgba(155, 89, 182, 0.8) transparent;
}

/* Webkit browsers (Chrome, Safari, Edge) */
.overlay-scrollbar::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.overlay-scrollbar::-webkit-scrollbar-track {
  background: transparent;
}

.overlay-scrollbar::-webkit-scrollbar-thumb {
  background-color: rgba(155, 89, 182, 0.8);
  border-radius: 10px;
  border: 2px solid transparent;
  background-clip: content-box;
}

/* Aura animation effects */
@keyframes aura-pulse {
  0% {
    transform: scale(1);
    opacity: 0.5;
    box-shadow: 0 0 20px 10px rgba(192, 132, 252, 0.6);
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
    box-shadow: 0 0 30px 15px rgba(192, 132, 252, 0.8);
  }
  100% {
    transform: scale(1);
    opacity: 0.5;
    box-shadow: 0 0 20px 10px rgba(192, 132, 252, 0.6);
  }
}

@keyframes aura-glow {
  0% {
    box-shadow: 0 0 10px 5px rgba(147, 51, 234, 0.5);
  }
  50% {
    box-shadow: 0 0 20px 10px rgba(147, 51, 234, 0.7);
  }
  100% {
    box-shadow: 0 0 10px 5px rgba(147, 51, 234, 0.5);
  }
}

/* Aura classes for direct application */
.aura-effect {
  position: relative;
  border: 3px solid rgba(192, 132, 252, 0.5);
  box-shadow: 0 0 20px 10px rgba(192, 132, 252, 0.6);
  animation: aura-pulse 3s infinite ease-in-out;
}

.aura-glow {
  animation: aura-glow 4s infinite ease-in-out;
}

/* Pulsing effect for the aura layers */
.animate-pulse-slow {
  animation: pulse-slow 4s infinite;
}

@keyframes pulse-slow {
  0% {
    opacity: 0.4;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.05);
  }
  100% {
    opacity: 0.4;
    transform: scale(1);
  }
}

.animate-pulse {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0.3;
  }
}